<template>
    <div id="r-provision" :style="`height: ${maxHeight}px; overflow: auto`">
      <div class="r-card-mindmap" >
         <div class="r-card" v-for="parent in provisionData.parents" :key="parent.id" @click="setPolicyId(parent.id)">
            <div class="r-circle-name" :class="classType(parent.category)">
                {{shortName(parent.category)}}
            </div>
            <span>{{parent.name}}</span>
            <img class="r-card-icon" :src="require('@/assets/arrow_drop_down_circle_24px.svg')" alt="">
        </div>
         <div :class="data.parents.length > 0 ? 'r-subcard-mindmap' : ''">
            <div class="r-card r-isactive-green" >
                <div class="r-circle-name" :class="classType(infoData.category)">
                    {{shortName(infoData.category)}}
                </div>
                <span>{{infoData.title}}</span>
                <img class="r-card-icon" :src="require('@/assets/arrow_drop_down_circle_24px.svg')" alt="">
            </div>
              <div class="r-subcard-mindmap" v-for="child in provisionData.child" :key="child.id">
                <div class="r-card" @click="setPolicyId(child.id)">
                    <div class="r-circle-name" :class="classType(child.category)">
                        {{shortName(child.category)}}
                    </div>
                    <span>{{child.name}}</span>
                    <img class="r-card-icon" :src="require('@/assets/arrow_drop_down_circle_24px.svg')" alt="">
                </div>
              </div>
          </div>
      </div>
    </div>
</template>
<script>
export default {
  props:{
    data: {
      type: Object,
      default: () => ({
        name: null
      })
    },
    info: {
      type: Object,
      default: () => ({
        title: null
      })
    }
  },
  methods: {
    classType(category){
      var type = this.shortName(category)
      if(type === 'K'){
          return 'r-purple-bg'
      }else if (type === 'MPO'){
          return 'r-blue-bg'
      }else if (type === 'PTO'){
          return 'r-green-bg'
      }else if (type === 'SP'){
          return 'r-red-bg'
      }else{
          return 'r-gray-bg'
      }
    },
    setPolicyId(id){
      this.$store.commit('setPolicyDetailId',id)
    },
    shortName(name)
    {
        var str = name;
        var t = str.split(' ')
        var short_name = ""
        for(let i = 0; i< t.length; i++){
          short_name = short_name + t[i][0]
        }
        return short_name;
    }
  },
  computed: {
    maxHeight(){
      return window.innerHeight - 280;
    },
    provisionData(){
      return this.data
    },
    infoData(){
      return this.info
    }
  },
}
</script>
<style lang="scss">
#r-provision{
    .r-card{
        max-width: 568px;
    }
}
</style>